.reservations-container {
  margin-left: 2rem;
  margin-right: 2rem;
  margin-bottom: 3rem;
  font-family: 'Helvetica';
}

.header {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  padding: 5px 20px;
  font-family: 'Helvetica';
  font-weight: bold;
  background-color: lightgray;
}

.column-header {
  width: 15%;
}

.button-column {
  width: 20%;
}

.room-number-column-header, .price-column-header {
  width: 10%;
}

.rewards-points-container {
  font-family: 'Helvetica';
  align-items: center;
  float: right;
  margin-right: 20px;
  font-size: 1.5rem;
  font-weight: 800;
}